@import 'libs/reset.scss';

body {
  background: #fff;
  font-size: 18px;
  font-family: Inter, sans-serif;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: #222934;
  display: inline-block;
}

img {
  display: block;
}

a:hover {
  color: #124594;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.spacer {
  margin-top: 160px;
}

.hero {
  position: relative;
  &__navigation {
    padding-top: 62px;
    display: flex;
    align-items: center;
    &-link {
      &:first-child {
        margin-left: 0;
      }
      margin-left: 40px;
    }
  }
  &__search {
    cursor: pointer;
  }
  &__logo {
    position: absolute;
    top: 40px;
    left: 40px;
  }
  &__search {
    position: absolute;
    top: 62px;
    right: 40px;
  }
}

.blog-list {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__list-item {
    width: 270px;
    margin-top: 60px;
    p {
      margin-top: 15px;
      font-size: 14px;
      color: #4d4d4d;
    }
  }

  &__list-image {
    img {
      pointer-events: none;
      height: 280px;
      object-fit: cover;
      width: 100%;
      border-radius: 5px;
    }
  }

  &__list-title {
    margin-top: 15px;
    font-weight: 600;
    color: #222934;
  }

  &__list-item--long {
    width: 580px;
    img {
      height: 240px;
    }
  }
  h1 {
    margin: 0;
    &::after {
      margin-top: 15px;
      content: '';
      width: 100%;
      height: 2px;
      display: block;
      background: #124594;
    }
  }
}

h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 42px;
}

.search-modal {
  &--active {
    opacity: 1;
    z-index: 9999;
  }
  position: fixed;
  transition: .2s all ease-out;
  opacity: 0;
  z-index: -1;
  top: 0;
  left: 0;
  background: #15181d;
  width: 100%;
  height: 100%;
  &__field {
    opacity: 0;
    &--fadeInDown {
      opacity: 1;
      animation: fadeInDown 1 .5s ease-out;
    }
    animation: fadeOutDown 1 .5s ease-out;
    margin: 0 auto;
    margin-top: 266px;
    width: 712px;
    position: relative;
    input {
      padding: 0;
      width: 712px;
      color: #fff;
      border: none;
      background: transparent;
      font-style: normal;
      font-weight: normal;
      font-size: 25px;
      line-height: 120%;
      padding-bottom: 15px;
      border-bottom: 2px solid #888888;
      transform: .3s all ease-out;
      &:focus {
        border-color: #fff;
      }
    }
  }
  &__btn {
    position: absolute;
    right: 0;
    padding: 0;
    margin: 0;
    bottom: 15px;
    width: 36px;
    height: 36px;
    background-color: transparent !important;
    border: none;
  }
  &__close {
    position: absolute;
    right: 40px;
    top: 62px;
    cursor: pointer;
    transition: .3s all ease-out;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}